export default {
	data() {
		return {
			Router:this.$Router,
			Utils:this.$Utils,
			mainData:[],
			searchItem:{
				check_status:0,
				user_type:0
			},
			indexOne:1,
		}
	},
	
	onLoad(options) {
		const self = this;
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
		self.searchItem.shop_no = uni.getStorageSync('merchantInfo').user_no;
		self.$Utils.loadAll(['getMainData'], self)
	},
	
	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},

	methods: {
		
		notice(index) {
			const self = this;
			uni.setStorageSync('canClick', false);
			console.log(index)
			const postData = {};
			postData.data = {
				order_id:self.mainData[index].id
			};
			postData.tokenFuncName = 'getMerchantToken';
			const callback = (res) => {
				uni.setStorageSync('canClick', true);
				if(res.solely_code==100000){
					self.$Utils.showToast('已发送短信提醒', 'none');
				}else{
					self.$Utils.showToast(res.msg, 'none');
				}
			};
			self.$apis.notice(postData, callback);
		},
		
		changeone(index){
			const self=this;
			if(index!=self.indexOne){
				self.indexOne = index
				if(self.indexOne==1){
					self.searchItem.check_status = 0
				}else{
					self.searchItem.check_status = 1
				};
				self.getMainData(true)
			}
		},
		
		getMainData(isNew) {
			const self = this;
			if (isNew) {
				self.mainData = [];
				self.paginate = {
					count: 0,
					currentPage: 1,
					pagesize: 5,
					is_page: true,
				};
			};
			const postData = {};
			postData.paginate = self.$Utils.cloneForm(self.paginate);
			postData.searchItem = self.$Utils.cloneForm(self.searchItem);
			postData.searchItem.pay_status = 1;
			postData.tokenFuncName = 'getMerchantToken';
			postData.getAfter = {
				user:{
					tableName:'User',
					middleKey:'user_no',
					key:'user_no',
					searchItem:{
						status:1
					},
					condition:'='
				}
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				console.log(self.mainData)
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.orderGet(postData, callback);
		},
		
	}
}
